

import "./Css/Index.less"

var FromDesign = {

    //
    init(Opts){

        var $el = $('<div class="desiContent"/>');
        
        $el.append(this.reToDoHtml());
        
        $el.append('<div class="fromDesign"/>'); 

        Opts.$el.append($el);

    },

    reToDoHtml(){
        return '<div class="reToDo">' +
        '<span class="btnReDo empty"><i class="commoniconfont icon-chexiao"></i><span class="text"></span></span>'+
        ' <span class="btnToDo empty"><i class="commoniconfont icon-huifu1"></i><span class="text"></span></span></div>'; 
    },

}

export default FromDesign;


